@import url('https://fonts.googleapis.com/css2?family=Eras+Light+ITC&family=Roboto+Condensed:wght@300;400;700&display=swap');

body {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  padding: 0;
  position: relative;
}

.header-content {
  width: 100%;
  padding: 20px 0;
  background-color: #9c1c2d;
  border-radius: 0 0 50% 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.theme-toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  background-color: #e6e6e6;
  color: #323234;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

h1 {
  font-family: 'Eras Light ITC', sans-serif;
  margin: 0;
  color: #e6e6e6;
}

h2, h3 {
  color: inherit;
}

.About-me, .Portfolio, .Contact {
  margin: 20px auto;
  padding: 20px;
  background-color: inherit;
  border-radius: 10px;
  max-width: 1200px; /* Increased max-width for more space */
  box-sizing: border-box;
}

a {
  color: #9c1c2d;
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}

.project-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.project-image {
  width: 200px;
  height: 200px;
  margin-right: 20px;
  border-radius: 10px; /* Soft corners for project images */
}

.project-details {
  flex-grow: 1;
  text-align: left;
}

.project-title {
  margin-top: 0; /* Ensure proper alignment with the image */
}

/* Updated styles for the project features list */
.project-features {
  display: flex;
  flex-wrap: wrap;
  list-style-type: disc;
  padding-left: 20px; /* Adding padding for bullet points */
}

.project-features li {
  width: 50%; /* Two columns */
  margin: 0; /* Remove spacing between points */
  padding: 0; /* Remove padding between points */
}

/* Specific style for the Movie Database App image */
.movie-db-image {
  height: 350px; /* New height to Movie Database App */
}

.contact-icon {
  width: 56px;
  height: 56px;
  margin-right: 10px;
  vertical-align: middle;
}

.contact-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-row p {
  margin: 0 10px;
}

.dark-mode {
  background-color: #323234;
  color: #e6e6e6;
}

.light-mode {
  background-color: #d3d3d3;
  color: #323234;
}

.light-mode .App-header h1 {
  color: #e6e6e6;
}